.hero {
  position: relative;
  @include after {
    position: absolute;
    top: 0;
    bottom: 50px;
    right: 0;
    left: percentage(1335/1920);
    background-color: $black-light; }
  &__inner {
    @include flex(flex-start, normal, row, wrap);
    margin-left: -15px;
    margin-right: -15px;
    position: relative;
    z-index: 1;
    padding-top: $header-height;
    height: 100vh;
    height: calc(var(--vh, 1vh) * 100); }
  &__left, &__right {
    padding-left: 15px;
    padding-right: 15px; }
  &__left {
    width: percentage(5/12);
    padding-top: 30px;
    @include md {
      padding-top: 15px; } }
  &__right {
    width: percentage(7/12); }
  &__img {
    @include cover;
    // padding-top: 100%
    display: block;
    height: 100%; }
  &__logo {
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; } }
  &__text {
    margin-bottom: 60px;
    @include xl {
      margin-bottom: 50px; }
    @include lg {
      margin-bottom: 40px; }
    @include md {
      margin-bottom: 20px; } }
  &__btns {
    margin-bottom: 10%;
    @include flex(flex-start, center); }
  &__btn {
    &:first-child {
      margin-right: 50px; } }
  &__social {
    .social {
      display: inline-block;
      position: relative;
      vertical-align: middle;
      margin-left: 10px; } } }

